import React, { useRef, useEffect } from "react";
import PropTypes from "prop-types";
// import { Link } from 'gatsby';
import { Marker, useMap } from "react-leaflet";
import L from "leaflet";

import Layout from "../components/layouts/layout"
import Container from "../components/container"
import Section from "../components/section"
import Link from "../components/Link"
import { promiseToFlyTo } from "../leaflet/lib/map";
import Map from '../components/map';

const LOCATION = {
  lat: 47.28347960537729,
  lng: 7.940711975097656,
};
const CENTER = [LOCATION.lat, LOCATION.lng];
const DEFAULT_ZOOM = 7;
const ZOOM = 15;

const mapSettings = {
  center: CENTER,
  defaultBaseMap: "OpenStreetMap",
  zoom: DEFAULT_ZOOM,
};

const timeToZoom = 2500;
const timeToOpenPopupAfterZoom = 2000;
const timeToUpdatePopupAfterZoom = timeToOpenPopupAfterZoom + 3000;

const popupContentHello = `<p>BZZ 🤾</p>`;
const popupContentGatsby = `
  <div className="content">
    <h1>BZZ</h1>
    <p>Strengelbacherstrasse Zofingen</p>
  </div>
`;
// <img className="gatsby-astronaut" src=${gatsby_astronaut} />

/**
 * MapEffect
 * @description This is an example of creating an effect used to zoom in and set a popup on load
 */

const MapEffect = ({ markerRef }) => {
  const map = useMap();

  useEffect(() => {
    if (!markerRef.current || !map) return;

    (async function run() {
      const popup = L.popup({
        maxWidth: 800,
      });

      // const location = await getCurrentLocation().catch(() => LOCATION);

      const { current: marker } = markerRef || {};

      // marker.setLatLng(location);
      // popup.setLatLng(location);
      popup.setContent(popupContentHello);

      setTimeout(async () => {
        await promiseToFlyTo(map, {
          zoom: ZOOM,
          center: LOCATION,
        });

        marker.bindPopup(popup);

        setTimeout(() => marker.openPopup(), timeToOpenPopupAfterZoom);
        setTimeout(
          () => marker.setPopupContent(popupContentGatsby),
          timeToUpdatePopupAfterZoom
        );
      }, timeToZoom);
    })();
  }, [map, markerRef]);

  return null;
};

MapEffect.propTypes = {
  markerRef: PropTypes.object,
};

const Halle = () => {
  const markerRef = useRef();

  return (
    <Layout title={"Halle"}>
    <Section>
      <Container>
      <Map {...mapSettings}>
        <MapEffect markerRef={markerRef} />
        <Marker ref={markerRef} position={CENTER} />
      </Map>

      <div className="content">
        <p>
          Der TV Zofingen bestreitet seine Heimspiele in der Halle BZZ.
          <ul>
            <li><Link to="https://www.handball.ch/de/matchcenter/hallen/140961#/">Hallenübersicht SHV</Link></li>
            <li><Link to="https://www.handball.ch/de/matchcenter/hallen/140961#/schedule">Spiele im BZZ</Link></li>
          </ul>
        </p>
      </div>
    </Container>
    </Section>
    </Layout>
  )
}

export const Head = () => <title>Halle</title>

export default Halle
